body{
    font-style: 'Arial', sans-serif;
    background-color: #a657b9;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    padding: 0;

}
.container{
    background-color: #501f3de3;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px rgba(0,0,0,0.1);
    width: 100%;
    max-width: 400px;

}
h2{
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
}
label{
    display: block;

}
input[type="email"],
input[type="password"]{
    width: 80%;
    padding: 10px;
    margin: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}
button
{
    background-color:rgb(247,7,27) ;
    color: white;
    margin-top: 20px;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    
}